// stylelint-disable selector-no-qualifying-type

// Make the div behave like a button
.btn-group,
.btn-group-vertical {
  position: relative;
  display: inline-flex;
  vertical-align: middle; // match .btn alignment given font-size hack above

  > .btn {
    position: relative;
    flex: 1 1 auto;

    // Bring the hover, focused, and "active" buttons to the front to overlay
    // the borders properly
    &:hover,
    &:focus,
    &:active,
    &.active {
      z-index: 1;
    }
  }
}

// Optional: Group multiple button groups together for a toolbar
.btn-toolbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

  .input-group {
    width: auto;
  }
}

.btn-group {
  // Prevent double borders when buttons are next to each other
  > .btn:not(:first-child),
  > .btn-group:not(:first-child) {
    margin-left: -$btn-border-width;
  }

  // Reset rounded corners
  > .btn:not(:last-child):not(.dropdown-toggle),
  > .btn-group:not(:last-child) > .btn {
    @include border-right-radius(null); // Boosted mod: no rounded corners to reset
  }

  > .btn:not(:first-child),
  > .btn-group:not(:first-child) > .btn {
    @include border-left-radius(null); // Boosted mod: no rounded corners to reset
  }
}

// Sizing
//
// Remix the default button sizing classes into new ones for easier manipulation.

.btn-group-sm > .btn { @extend .btn-sm; }
.btn-group-lg > .btn { @extend .btn-lg; }


//
// Split button dropdowns
//

.dropdown-toggle-split {
  padding-right: subtract($dropdown-padding-x, $border-width);
  padding-left: subtract($dropdown-padding-x, $border-width);
  border-color: $black;

  // Boosted mod
  &:not(:hover):not(:focus):not(:active) {
    border-left-color: transparent;
  }

  &:not(:focus):not(:active)::before {
    position: absolute;
    top: -$border-width;
    bottom: -$border-width;
    left: -$border-width;
    width: $border-width;
    color: $black;
    content: "";
    background:
      linear-gradient(
        currentColor $border-width,
        transparent $border-width,
        transparent $border-width * 2.5,
        currentColor $border-width * 2.5,
        currentColor subtract(100%, $border-width * 2.5),
        transparent subtract(100%, $border-width * 2.5),
        transparent subtract(100%, $border-width),
        currentColor subtract(100%, $border-width)
      );
  }

  .btn:active + & {
    border-color: $primary;
  }

  .btn-group.show > &:not(:focus):not(:active)::before {
    color: $primary;
    background-color: currentColor;
  }

  .dropleft > &:not(:hover):not(:focus):not(:active) {
    border-left-color: $black;
  }
  // End mod

  &::after,
  .dropup &::after,
  .dropright &::after {
    margin-left: 0;
  }

  .dropleft &::before {
    margin-right: 0;
  }
}

.btn-sm + .dropdown-toggle-split {
  padding-right: $btn-padding-x-sm * .5; // Boosted mod
  padding-left: $btn-padding-x-sm * .5; // Boosted mod
}

.btn-lg + .dropdown-toggle-split {
  padding-right: $btn-padding-x-lg * .5; // Boosted mod
  padding-left: $btn-padding-x-lg * .5; // Boosted mod
}


// The clickable button for toggling the menu
// Set the same inset shadow as the :active state
.btn-group.show .dropdown-toggle {
  @include box-shadow($btn-active-box-shadow);

  // Show no shadow for `.btn-link` since it has no other button styles.
  &.btn-link {
    @include box-shadow(null); // Boosted mod: no box-shadow to unset
  }
}


//
// Vertical button groups
//

.btn-group-vertical {
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  > .btn,
  > .btn-group {
    width: 100%;
  }

  > .btn:not(:first-child),
  > .btn-group:not(:first-child) {
    margin-top: -$btn-border-width;
  }

  // Reset rounded corners
  > .btn:not(:last-child):not(.dropdown-toggle),
  > .btn-group:not(:last-child) > .btn {
    @include border-bottom-radius(null); // Boosted mod: no rounded corners to reset
  }

  > .btn:not(:first-child),
  > .btn-group:not(:first-child) > .btn {
    @include border-top-radius(null); // Boosted mod: no rounded corners to reset
  }
}


// Checkbox and radio options
//
// In order to support the browser's form validation feedback, powered by the
// `required` attribute, we have to "hide" the inputs via `clip`. We cannot use
// `display: none;` or `visibility: hidden;` as that also hides the popover.
// Simply visually hiding the inputs via `opacity` would leave them clickable in
// certain cases which is prevented by using `clip` and `pointer-events`.
// This way, we ensure a DOM element is visible to position the popover from.
//
// See https://github.com/twbs/bootstrap/pull/12794 and
// https://github.com/twbs/bootstrap/pull/14559 for more information.

.btn-group-toggle {
  > .btn,
  > .btn-group > .btn {
    border-color: $gray-500; // Boosted mod

    // Boosted mod: hover = focus, active  .active, focus  focus-visible
    &:hover,
    &:focus,
    &.focus {
      color: $primary;
      background-color: transparent;
      border-color: $gray-500;
    }

    // stylelint-disable selector-max-class, declaration-no-important
    &:active,
    &.active.focus {
      color: $white;
      background-color: $primary;
      border-color: $primary;
      outline: $btn-border-width solid $primary !important;
    }

    &.active {
      color: $white;
      background-color: $black;
      border-color: $black;
      outline-color: $black !important;
    }
    // stylelint-enable selector-max-class, declaration-no-important
    // End mod

    input[type="radio"],
    input[type="checkbox"] {
      position: absolute;
      clip: rect(0, 0, 0, 0);
      pointer-events: none;
    }
  }

  // Boosted mod: no border inside of .btn-group-toggle
  > .btn:not(:last-child):not(.dropdown-toggle),
  > .btn-group:not(:last-child) > .btn {
    border-right: 0;
  }

  > .btn:not(:first-child),
  > .btn-group:not(:first-child) > .btn {
    border-left: 0;
  }

  > .btn:not(:first-child),
  > .btn-group:not(:first-child) {
    margin-left: 0;
  }
  // End mod
}
